<template>
  <div class="app-container">
    <el-form ref="ruleForm" :model="ruleForm" label-width="170px" class="demo-ruleForm">
      <el-form-item label="微信登陆是否需要绑定">
        <el-switch
          v-model="ruleForm.wechatNeedBinding"
          active-text="是"
        />
      </el-form-item>
      <el-form-item label="sina登陆是否需要绑定">
        <el-switch
          v-model="ruleForm.sinaNeedBinding"
          active-text="是"
        />
      </el-form-item>
      <el-form-item label="qq登陆是否需要绑定">
        <el-switch
          v-model="ruleForm.qqNeedBinding"
          active-text="是"
        />
      </el-form-item>
      <!-- <el-form-item> -->
      <el-button type="primary" @click="submitForm('ruleForm')">
        {{ $t('common.comfirm') }}
      </el-button>
      <el-button @click="getData">
        {{ $t('common.cancel') }}
      </el-button>
      <!-- </el-form-item> -->
    </el-form>
  </div>
</template>

<script>
import { fetchData, saveData } from '@/api/sysManage/socialManage/sociaEdit'
import waves from '@/directive/waves' // Waves directive

export default {
  name: 'SociaEdit',
  directives: { waves },
  data() {
    return {
      ruleForm: {}
    }
  },
  created() {
    this.getData()
  },
  methods: {
    getData() {
      this.listLoading = true
      fetchData().then(response => {
        console.log(response)
        this.ruleForm = response.config
      })
    },
    submitForm() {
      saveData(this.ruleForm).then(response => {
        this.$message({
          type: 'success',
          message: '保存成功'
        })
      })
    }
  }
}
</script>

<style>
.sortable-ghost{
  opacity: .8;
  color: #fff!important;
  background: #42b983!important;
}
</style>

<style scoped>
.icon-star{
  margin-right:2px;
}
.drag-handler{
  width: 20px;
  height: 20px;
  cursor: pointer;
}
.show-d{
  margin-top: 15px;
}
</style>
